import React from "react";
import { Button, Col } from "antd";
import useFormValidate from "../../../core/hook/useFormValidate";
import { useDispatch } from "react-redux";
import InputGroup from "../../Atoms/InputGroup";
import { register } from "../../../redux/reducers/authReducer";

export default function Register() {
  let dispatch = useDispatch();

  let { error, form, submit, inputChange } = useFormValidate(
    {
      first_name: "",
      last_name: "",
      username: "",
      password: "",
      confirm_password: "",
    },
    {
      rule: {
        first_name: {
          required: true,
        },
        last_name: {
          required: true,
        },
        username: {
          required: true,
          pattern: "email",
        },
        password: {
          required: true,
          min: 6,
          max: 32,
        },
        confirm_password: {
          required: true,
          match: "password",
        },
      },
      message: {
        first_name: {
          required: "H khng c  trng",
        },
        last_name: {
          required: "Tn khng c  trng",
        },
        username: {
          required: "Email khng c  trng",
          pattern: "Email khng ng nh dng",
        },
        confirm_password: {
          required: "Vui lng xc nhn mt khu",
          match: "Vui lng in ging password",
        },
      },
    }
  );

  function _register() {
    error = submit();
    if (Object.keys(error).length === 0) {
      console.log(form);
      dispatch(register(form));
    }
  }

  return (
    <Col
      xl={{ span: 12 }}
      lg={{ span: 12 }}
      md={{ span: 24 }}
      sm={{ span: 24 }}
      xs={{ span: 24 }}
      className="register"
      style={{ padding: "50px" }}
    >
      <p className="register-title">Khch hng mi</p>
      <InputGroup
        type="text"
        placeholder="First Name"
        inputChange={inputChange}
        name="first_name"
        required
        error={error}
      />
      <InputGroup
        type="text"
        placeholder="Last Name"
        inputChange={inputChange}
        name="last_name"
        required
        error={error}
      />
      <InputGroup
        type="text"
        placeholder="Email Address"
        inputChange={inputChange}
        name="username"
        required
        error={error}
      />
      <InputGroup
        type="password"
        placeholder="Password"
        inputChange={inputChange}
        name="password"
        required
        error={error}
      />
      <InputGroup
        type="password"
        placeholder="Confirm Password"
        inputChange={inputChange}
        name="confirm_password"
        required
        error={error}
      />

      <p className="register-confirm">
        Bng cch ng k, bn  ng  vi iu khon v chnh sch bo mt
        ca chng ti.
      </p>
      <div className="register-row">
        <Button
          type="submit"
          className="login-row-btnSubmit"
          size="large"
          onClick={_register}
        >
          ng k
        </Button>
      </div>
    </Col>
  );
}
